:root {
    --cursor-pointer: default
}

.image-wrapper {
    width: 100%;
    position: relative;
}

.image-wrapper:hover {
    cursor: var(--cursor-pointer)
}

.image-wrapper img {
    width: 100%;
    pointer-events: all;
}

.image-wrapper img:hover {
    cursor: var(--cursor-pointer)
}