:root {
    --swiper-theme-color: #fbf40a;
    --swiper-navigation-size: 25px;
    --swiper-pagination-bullet-inactive-color: white;
}

html,
body {
    position: relative;
}

.swiper-slide {
    font-size: 18px;
    position: relative;
    text-align: center;
    border: 2px solid #feffb7;
    transition: 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper-slide:hover {
    transform: scale(1.03);
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    max-width: 255px;
    object-fit: cover;
    cursor: pointer;
}

.swiper-slide>p {
    left: 0;
    bottom: 0px;
    font-size: 1rem;
    color: honeydew;
    position: absolute;
    font-family: sans-serif;
    text-decoration: overline;
    text-decoration-thickness: 2px;
}

.swiper {
    padding: 10px;
    margin: 0 auto;
    margin-top: 70px;
    max-width: 860px;
    margin-bottom: 70px;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 6px black;
}

@media screen and (max-width: 750px) {
    .swiper {
        max-width: 545px;
    }
}

@media screen and (max-width: 500px) {
    .swiper {
        max-width: 280px;
    }
}